import * as React from 'react';
import * as styles from './AnchorButton.module.scss';
export const AnchorButton = ({ children, id })=>{
    function scroll(query) {
        document.querySelector(query).scrollIntoView({
            behavior: 'smooth'
        });
    }
    return React.createElement("button", {
        onClick: ()=>scroll(id),
        className: styles.button
    }, children);
};
